
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.add {
  width: 100px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-bottom: 50px;
  margin-left: 50px;
  background: transparent;
  border: 2px solid #2c3e50;
  border-radius: 5px;
}
.add:hover {
  cursor: pointer;
}
.wrap {
  width: 750px;
  height: 300px;
  background: #555555;
  position: relative;
}
.wrap .track {
  position: relative;
  width: 100%;
  height: 30px;
  margin-bottom: 20px;
  color: #ffffff;
}
.text {
  position: absolute;
  text-align: left;
  top: 50%;
  left: 100%;
  white-space: nowrap;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  font-size: 18px;
  background: rgba(255, 255, 255, 8);
  padding: 5px 15px;
  border-radius: 50px;
  color: #555555;
  /* animation-fill-mode: forwards; */
}
.left-enter-active {
  -webkit-animation: moveLeft 5s linear;
          animation: moveLeft 5s linear;
}
@-webkit-keyframes moveLeft {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(-1800px);
            transform: translateX(-1800px);
}
}
@keyframes moveLeft {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(-1800px);
            transform: translateX(-1800px);
}
}

